body {
  margin: 0px;
  padding: 0px;
  background: linear-gradient(150deg,#0099ff,#7400a5);
  height: 100vh;
  overflow: hidden;
}

.app {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 800px;
  height: 400px;
background: rgba(255, 255, 255, 0.1);
border-radius: 16px;
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(5px);
border: 1px solid rgba(255, 255, 255, 0.3);
padding: 5px;
display: flex;
justify-content: space-around;
align-items: center;
}
.search {
  height: 90%;
  width: 40%;
  display: flex;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.1);
  flex-direction: column;
  position: relative;
  
}
.search h1 {
  text-align: center;
  padding: 0px 0px 10px;
  color: #ffffff;
  border-bottom: 2px solid rgba(192, 192, 192, 0.4);
}
.search .text {
  width: 80%;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  outline: none;
  border: none;
}
.search .button {
  color: white;
  background-color: #0099ff;
  border: none;
  width: 80%;
  height: 30px;
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.search .button:hover {
  background-color: #006eff;
}
.result {
  height: 90%;
  width: 40%;
  display: flex;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.1);
  flex-direction: column;
  position: relative; 
}
.city-name {
  text-align: center;
  color: white;
}
.icon {
  text-align: center;
}
.icon img {
  height: 80px;
}
.temp {
  font-size: 70px;
  color: white;
  text-align: center;
  font-family: 'Courier New', Courier, monospace;
}
.error {
  color: white;
  text-align: center;
}
@media (max-width : 767px) {
  body {
    margin: 0px;
    padding: 0px;
    background: linear-gradient(150deg,#0099ff,#7400a5);
  }
  .app {
    flex-direction: column;
    height: 550px;
    width: 90vw;
  }
  .search {
    height: 80%;
    width: 90%;
    margin-bottom: 10px;
  }
  .result {
    width: 90%;
  }
}


